import { login } from '../utils/api';

export const AUTHORIZE = 'AUTHORIZE';
export const CLEAR_ERROR = 'CLEAR_ERROR';

export function authorize(response) {
  return {
    type: AUTHORIZE,
    response,
  };
}

export function clearError() {
  return {
    type: CLEAR_ERROR,
  }
}

export function handleLogin(name, email, voucher) {
  return function(dispatch, getState) {
    const { mac, url } = getState().info;

    const loginInfo = {
      name,
      email,
      code: voucher,
      mac,
      url,
    };

    return login(loginInfo)
      .then(response => { dispatch(authorize(response.data)) })
      .catch(() => { dispatch(authorize({ validCode: false })) });
  };
}
