export function decorateVoucher(voucher, pos, length) {
    const dash = voucher.includes('-') === pos;

    if (dash === true) {
      voucher = [voucher.slice(0, pos), '-', voucher.slice(pos)].join('');
    }
    voucher = voucher.replace(/\D/g,'');

    if (voucher.length > 5 && dash === false) {
      voucher = [voucher.slice(0, pos), '-', voucher.slice(pos)].join('');
    }
    if (voucher.length > length) {
      voucher = voucher.substr(0, length + 1);
    }

    return voucher;
}
