import { AUTHORIZE, CLEAR_ERROR } from '../actions/login';

export default function login(state = {}, action) {
  switch (action.type) {
    case AUTHORIZE:
      return {
        ...state,
        ...action.response,
      };
    case CLEAR_ERROR:
      return {
        ...state,
        validCode: true,
      };
    default:
      return state;
  }
}
