import React, { Component } from 'react';
import './Login.css';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';
import { decorateVoucher } from '../utils/helper';
import { connect } from 'react-redux';
import { addInfo } from '../actions/info';
import { handleLogin, clearError } from '../actions/login';


class Login extends Component {
  state = {
    firstname: '',
    lastname: '',
    email: '',
    voucher: '',
  }

  componentDidMount() {
    const values = queryString.parse(this.props.location.search);
    const info = {
      mac: values.id,
      url: values.url,
    }
    this.props.dispatch(addInfo(info));
  }

  handleChange = (event) => {
    const { dispatch, error } = this.props;
    const name = event.target.name;
    var value = event.target.value;

    if (name === 'voucher') {
      value = decorateVoucher(value, 5, 10);

      if (error === true) {
        dispatch(clearError());
      }
    }

    this.setState((previousState) => {
      if (name === 'voucher') {
        previousState.error = false;
      }
      previousState[name] = value;
      return previousState;
    });
  }

  handleSubmit = (event) => {
    const { firstname, lastname, email, voucher } = this.state;
    const { dispatch } = this.props;

    event.preventDefault();
    const name = firstname + ' ' + lastname;
    dispatch(handleLogin(name, email, voucher));
  }

  render() {

    const { firstname, lastname, email, voucher } = this.state;
    const { error } = this.props;
    const disabled = firstname.length === 0 || lastname.length === 0 || email.length === 0 || voucher.length !== 11 || error === true;

    return (
      <div className="Login">
          <form onSubmit={this.handleSubmit}>
              <label>
                  First name:<br/>
                  <input
                    id="firstname"
                    type="text"
                    name="firstname"
                    value={firstname}
                    onChange={this.handleChange}
                  />
              </label><br/>
              <label>
                  Last name:<br/>
                  <input
                    id="lastname"
                    type="text"
                    name="lastname"
                    value={lastname}
                    onChange={this.handleChange}
                  />
              </label><br/>
              <label>
                  E-Mail:<br/>
                  <input
                    id="email"
                    type="email"
                    name="email"
                    value={email}
                    onChange={this.handleChange}
                  />
              </label><br/>
              <label>
                  Voucher:<br/>
                  <input
                    id="voucher"
                    className={error === true ? 'error' : ''}
                    type="text"
                    name="voucher"
                    value={voucher}
                    onChange={this.handleChange}
                  />
              </label><br/><br/>
              <input
                className="button"
                disabled={disabled}
                type="submit"
                value="LOGIN"
              />
          </form>
      </div>
    );

  }
}

Login.propTypes = {
  location: PropTypes.object.isRequired,
}

function mapStateToProps({ login }) {
  return {
    error: login && login.validCode === false
  }
}

export default withRouter(connect(mapStateToProps)(Login));
